import axiosRequest from '@/utils/AxiosRequest';

export function getUserList() {
    return axiosRequest({url: '/user/info', method: 'GET'});
}

export function updateUserName(params) {
  return axiosRequest({url: '/user/update-name', method: 'POST', data: params});
}

export function updateSecret(params) {
  return axiosRequest({url: '/user/update-secret', method: 'POST', data: params});
}

export function updateMobile(params) {
  return axiosRequest({url: '/user/update-mobile', method: 'POST', data: params});
}

export function updateEmail(params) {
  return axiosRequest({url: '/user/update-email', method: 'POST', data: params});
}

export function sendNewMobileCode(params) {
  return axiosRequest({url: '/user/send-mobile-code', method: 'POST', data: params});
}

export function sendOldMobileCode() {
  return axiosRequest({url: '/user/origin-mobile-code', method: 'POST'});
}

export function validateOldMobileCode(params) {
  return axiosRequest({url: '/user/validate-origin-mobile-code', method: 'POST', data: params});
}

export function sendEmailCode(params) {
  return axiosRequest({url: '/user/send-email-code', method: 'POST', data: params});
}

export function deleteContact(params) {
  return axiosRequest({url: '/user/contact-delete', method: 'POST', data: params});
}

export function addContact(params) {
  return axiosRequest({url: '/user/contact', method: 'POST', data: params});
}

export function getContactList() {
  return axiosRequest({url: '/user/contact-list', method: 'GET'});
}

export function addProxy(params) {
  return axiosRequest({url: '/user/proxy-add', method: 'POST', data: params});
}

export function getProxyList() {
  return axiosRequest({url: '/user/proxy-list', method: 'GET'});
}

export function deleteProxy(params) {
  return axiosRequest({url: '/user/proxy-delete', method: 'POST', data: params});
}

export function updateProxy(params) {
  return axiosRequest({url: '/user/proxy-update', method: 'POST', data: params});
}


export function getProxyUserList(params) {
  return axiosRequest({url: '/selector/user', params, method: 'GET'});
}

export function getWorkflowList(params) {
  return axiosRequest({url: '/selector/workflow',params, method: 'GET'});
}

export function getDeviceList() {
  return axiosRequest({url: '/user/device-list', method: 'GET'});
}

export function deleteDevice(params) {
  return axiosRequest({url: '/user/device-delete', method: 'POST', data: params});
}

export function getCompanyList() {
  return axiosRequest({url: '/user/company-list', method: 'GET'});
}

export function quitCompany(params) {
  return axiosRequest({url: '/user/company-quit', method: 'POST', data: params});
}

export function getLoginUserInfo(params) {
  return axiosRequest({url: '/authentication/login-user-info', params, method: 'GET'});
}

export function getThirdAccount(params) {
  return axiosRequest({url: '/user/third-account-status', params, method: 'GET'});
}

export function unbindingThirdAccount(params) {
  return axiosRequest({url: '/user/third-unbinding', method: 'POST', data: params});
}

export function logout() {
  return axiosRequest({url: '/authentication/logout', method: 'GET'});
}

export function getLoginLog() {
  return axiosRequest({url: '/user/sys-login-log', method: 'GET'});
}

export function createCompany(params) {
  return axiosRequest({url: '/company/create', method: 'POST', data: params});
}

export function joinCompany(params) {
  return axiosRequest({url: '/company/join', method: 'POST', data: params});
}

export function getOrganizeList(params) {
  return axiosRequest({url: '/selector/company', params, method: 'GET'});
}










