import Vue from 'vue';
import Router from 'vue-router';
import UserCenterIndex from '@/components/UserCenterIndex'

Vue.use(Router);

export const routerMap = [

  {
    path: '/login',
    name: 'loginLink',
    component: () => import('@/components/login/Login'),
    meta: {title: "用户中心",auth: false}
  },
  {
    path: '/no-allow',
    name: 'noallowLink',
    component: () => import('@/components/NoAllow'),
    meta: {title: "用户中心",auth: false}
  },
  {
    path: '/login-success',
    name: 'loginsuccessLink',
    component: () => import('@/components/login/LoginSuccess'),
    meta: { title: "用户中心",auth: false}
  },
  {
    path: '/login-failure',
    name: 'loginfailureLink',
    component: () => import('@/components/login/LoginFailure'),
    meta: { title: "用户中心",auth: false}
  },
    {
      path: '/',
      name: 'usercenterindex',
      component: UserCenterIndex,
      redirect: '/basic-information',
      meta: {
        title: "用户中心",
        auth: true
      },
    children: [
        {path: '/basic-information', name: 'informationLink', component: () => import('@/components/BasicInformation'), meta: { title: "用户中心",auth: true}},
        {path: '/agent-setup', name: 'agentsetupLink', component: () => import('@/components/AgentSetup'), meta: { title: "用户中心",auth: true}},
        {path: '/signature-setup', name: 'signaturesetupLink', component: () => import('@/components/SignatureSetup'), meta: { title: "用户中心",auth: true}},
        {path: '/personalized-setting', name: 'personalizedsettingLink', component: () => import('@/components/PersonalizedSetting'), meta: { title: "用户中心",auth: true}},
        {path: '/group-list', name: 'grouplistLink', component: () => import('@/components/GroupList'), meta: { title: "用户中心",auth: true}},
        {path: '/device-management', name: 'devicemanagementLink', component: () => import('@/components/DeviceManagement'), meta: { title: "用户中心",auth: true}},
        {path: '/login-log', name: 'loginlogLink', component: () => import('@/components/LoginLog'), meta: { title: "用户中心",auth: true}},
    ]
    }
];

export default new Router({
  routes: routerMap
});



