<template>
  <el-container>
    <el-header v-if="header_show"><center-header></center-header></el-header>
    <el-container>
      <el-aside width="200px" v-if="sidemenu_show"><side-menu ></side-menu></el-aside>
      <el-container >
        <el-main><router-view v-wechat-title="$route.meta.title"></router-view></el-main>
      </el-container>
    </el-container>
  </el-container>

</template>

<script>
  import  CenterHeader from  '@/components/CenterHeader'
  import  SideMenu from  '@/components/SideMenu'
    export default {
        name: "ConsoleIndex",
       showBottomNav:"",
        components:{
          centerHeader:CenterHeader,
          sideMenu:SideMenu
        },
      data(){
        return {
          header_show:true,
          sidemenu_show:true,
          contentStyleObj:{
            height:''
          }

        }
      },
      methods:{
        //是否显示头部
        public_header:function (bool) {
          this.header_show = bool;
        },
        //是否显示侧边导航
        public_footer:function (bool) {
          this.footer_show = bool;
        },
        getHeight(){
          this.contentStyleObj.height=window.innerHeight-70+'px';
        }
      },
      created(){
        window.addEventListener('resize', this.getHeight);
        this.getHeight()
      },

      destroyed(){
        window.removeEventListener('resize', this.getHeight)
      }

    }
</script>

<style scoped>

</style>



