import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
  loginUserInfo: null,
  requestHeader:null
};

const getters = {
  loginUserInfo: state => state.loginUserInfo,
  requestHeader: state => state.requestHeader
}
const mutations = {
  setLoginUserInfo(state, value) {
    state.loginUserInfo = value;
  },
  setHeaderObjectToken(state, value) {
    state.requestHeader = value;
  }
};

export default new Vuex.Store({state,getters, mutations});



