<template>
    <div class="side_memu" :style="contentStyleObj" style="overflow: hidden">
      <el-menu
        default-active="1"
        class="el-menu-vertical-demo"
        background-color="#1d2633"
        text-color="#fff"
        active-text-color="#00aaee">
        <el-menu-item index="2">
          <i class="icon iconfont iconiconfontzhizuobiaozhun023101"></i>
          <span slot="title"><router-link :to="{name:'informationLink'}">个人信息</router-link></span>
        </el-menu-item>
        <el-menu-item index="3">
          <i class="icon iconfont icondailiren"></i>
          <span slot="title"><router-link :to="{name:'agentsetupLink'}">代理人设置</router-link></span>
        </el-menu-item>
        <!--<el-menu-item index="4">-->
          <!--<i class="icon iconfont iconqianming"></i>-->
          <!--<span slot="title"><router-link :to="{name:'signaturesetupLink'}">签名设置</router-link></span>-->
        <!--</el-menu-item>-->
        <el-menu-item index="5">
          <i class="icon iconfont iconiconfontxuqiudingzhi"></i>
          <span slot="title"><router-link :to="{name:'personalizedsettingLink'}">第三方账号</router-link></span>
        </el-menu-item>
        <el-menu-item index="6">
          <i class="icon iconfont iconzuzhi"></i>
          <span slot="title"><router-link :to="{name:'grouplistLink'}">组织列表</router-link></span>
        </el-menu-item>
        <el-menu-item index="7">
          <i class="icon iconfont iconcaijiguanli"></i>
          <span slot="title"><router-link :to="{name:'devicemanagementLink'}">设备管理</router-link></span>
        </el-menu-item>
        <el-menu-item index="8">
          <i class="icon iconfont iconrizhi"></i>
          <span slot="title"><router-link :to="{name:'loginlogLink'}">登入日志</router-link></span>
        </el-menu-item>
      </el-menu>
    </div>
</template>

<script>
    export default {
        name: "SideMenu",
        data () {
          return {
            contentStyleObj:{
              height:''
            }
          }
        },

        methods:{
          getHeight(){
            this.contentStyleObj.height=window.innerHeight-60+'px';
          }
        },

        created(){
          window.addEventListener('resize', this.getHeight);
          this.getHeight()
        },

        destroyed(){
          window.removeEventListener('resize', this.getHeight)
        }
    }
</script>

<style scoped>
.side_memu{background-color: rgb(29, 38, 51);}
.el-menu {border-right:none;}
.el-menu-item i,.el-submenu__title i{color: #fff;    float: left;margin-right: 10px;}
.el-menu-item a,.el-menu-item span{color: #fff;cursor: pointer;    display: inline-block;width: 100%;}
.el-menu-item.is-active a{color: #00aaee;}
</style>



