import Axios from 'axios';
import Cookies from 'js-cookie'
import Router from '../router';
import ElementUI from 'element-ui';
import GlobalData from '../utils/Constant'

const axiosInstance = Axios.create({
  baseURL: GlobalData.baseUrl,
  timeout: 500000000,
});

axiosInstance.defaults.retry = 4;
axiosInstance.defaults.retryDelay = 1000;

axiosInstance.interceptors.request.use(config => {
  let accessToken = Cookies.get(GlobalData.requestToken);
  if (accessToken !== null) {
    config.headers[GlobalData.requestToken] = accessToken;
  }
  return config
}, error => {
  Promise.reject(error)
});

axiosInstance.interceptors.response.use(response => {
  if(response.data.code) {
    if(response.data.code === GlobalData.successCode) {
      return response;
    } else if (response.data.code === GlobalData.noLoginCode) {
      ElementUI.MessageBox.alert('会话已过期，请重新登录', '提示', {confirmButtonText: '确定', callback: action => {
          Router.push({ path: '/login' });
        }});
    }else  if(response.data.code === GlobalData.noAllowCode){
      Router.push({ path: '/no-allow' });
    }else {
      ElementUI.Message.error(response.data.message);
    }
  }
  return response;
}, error => {
  return Promise.reject(error)
});

export default axiosInstance;



